
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#app {
  margin-top: 0;
}
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 2px solid #000;
  border-radius: 0;
  background: #fff;
  padding: .5em 2em;
  font-size: 24px;
  cursor: pointer;
}
button:hover {
  color: #fff;
  background: #000;
}
.own-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  width: 250px;
}
.own-wrap-other {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.own-text {
  text-align: center;
  font-size: 20px;
}
#draw-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#draw {
  margin: 0 3em 1em;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.row > div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.grid {
  width: 50px;
  height: 50px;
  background: #ffad8f;
  cursor: pointer;
  border-radius: 4px;
  position: relative;
}
.grid[exists='other']:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  content: 'Network';
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background: #574bc2;
}
.grid[exists='self']:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  content: 'YOU';
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background: #73db65;
}
.grid[exists='possible']:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  content: '';
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background: rgba(115, 219, 101, 0.5);
}
.pawn-selected:after {
  content: 'NEXT'!important;
  background: rgba(255, 17, 0, 0.5) !important;
}
.wall_v {
  width: 10px;
  height: 50px;
  cursor: pointer;
  border-radius: 2px;
}
.wall_h {
  width: 50px;
  height: 10px;
  cursor: pointer;
  border-radius: 2px;
}
.wall_h[open='0'], .wall_v[open='0'] {
  background: #1a110e;
  border-radius: 0;
}
.wall-hover {
  background: rgba(26, 17, 14, 0.5);
  border-radius: 0;
}
.wall-selected {
  background: rgba(32, 253, 12, 0.5);
}
.blank {
  width: 10px;
  height: 10px;
  background: #1a110e;
}
.own_walls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.walls_other {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.own {
  width: 10px;
  height: 110px;
  background: #1a110e;
  margin: 0 3px;
}
.own_transepose {
  background: transparent !important;
}

